body {
	margin: 0 auto;
	padding: 0;
	width: 990px;
}

h1, h2, p, a{
	margin: 0;
	padding: 0;
}

.header {
	background: #61686c url('../img/bg.jpg') no-repeat center top / cover;
	color: #fff;
	height: 250px;

}

.nav {
	text-align: center;
}

.nav p {
	color: #fff;
}

.name{
	margin-top: 0;
	font-family: 'Oswald', sans-serif;
	font-size: 100px;
	margin-bottom: 0;
}

.poffeshn {
	margin-bottom: 0;
	margin-top: 0;
	font-family: 'Raleway', sans-serif;
	font-size: 70px;
}

.ball {
	background-color: #f7aab1;
    width: 70px;
    height: 70px;
   /* display: block;*/
    border-radius: 50%;
    line-height: 92px;
    margin-right: 20px;
    text-align: center;
}

.title {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
}

.container {
	background-color: #f8f8f8;
	padding-bottom: 20px;
}

.base {
	margin-left: 20px;
	position: relative;
}

.center {
	position: absolute;
	top: 240px;
	margin-left: 7%;
}

.centerpic {
	width: 800px;
}

.mainUP {
	/*display: flex;*/
	align-items: center;
	margin-top: 20px;
	/*margin-right: 20px;*/
	margin-bottom: 10px;
}

.mainUP h2 {
	font-family: 'Oswald' , sans-serif;
	font-weight: 600;
	font-size: 30px;
	
}

.mainDown {
	/*display: flex;*/
	align-items: center;
	margin-top: 170px;
	/*margin-right: 93px;*/
	margin-bottom: 10px;
}

.mainDown h2 {
	font-family: 'Oswald' , sans-serif;
	font-weight: 600;
	font-size: 30px;
}

.main1 {
	display: flex;
	justify-content: space-between;
}

.main2 {
	display: flex;
	justify-content: space-between;
}

.arrow {
	width: 40px;
}

.block {
	width: 300px;
	margin-right: 13px;
}

.block p {
	font-family: 'Alice', serif;
	font-size: 15px;
}

.list {
	margin-left: 334px;
    margin-top: -356px;
}

.list p {
	margin-top: 20px;
}

.list a {
	text-decoration: none;
	color: #f8f8f8;
	margin-top: 20px;
}

.footer {
	background: #f8f8f8 url('../img/footer.png') no-repeat center top / cover;
	height: 165px;
	position: relative;
}

.autor {
	position: absolute;
	bottom: 0;
	right: 0;
 }

@media (min-width: 1024px) and (max-width: 1280px) {
	body {
		width: auto;
		padding: 0 11px;
		min-width: 290px;
	}

	.centerpic {
		width: 800px;
	}

	.center {
		position: absolute;
		top: 240px;
		margin-left: 14.5%;
	}

	.list {
		margin-left: 340px;
    	margin-top: -355px;
	}
}

@media (min-width: 768px) and (max-width: 1024px) {
	body {
		width: auto;
		padding: 0 10px;
		min-width: 290px;
	}

	.ball {
		width: 40px;
	    height: 40px;
	    line-height: 45px;
	    margin-right: 10px;
	}

	.arrow {
		width: 20px;
	}

	.block {
		width: 210px;
		margin-right: 13px;
	}
	
	.block p {
		font-size: 10px;
		margin: 0;
	}

	.mainUP {
		margin-top: 10px;
		/*margin-right: 92px;*/
		margin-bottom: 10px;
	}

	.mainUP h2 {
		font-size: 20px;
	}

	.mainDown {
		margin-top: 160px;
		/*margin-right: 87px;*/
		margin-bottom: 10px;
	}

	.mainDown h2 {
		font-size: 20px;
	}

	.centerpic {
		width: 600px;
	}

	.center {
		position: absolute;
		top: 243px;
		margin-left: 11%;
	}

	.list {
		margin-left: 247px;
    	margin-top: -263px;
    	font-size: 20px;
	}

	.list a {
		font-size: 8px;
	}

	.list p {
		font-size: 8px;
		margin-top: 10px;
	}
}

@media (min-width: 480px) and (max-width: 768px) {
	
	body {
		width: auto;
		padding: 0 10px;
		min-width: 290px;
	}

	.base {
		margin-left: 0;
	}

	.header {
		height: 165px;
	}

	.center {
		display: none;
	}

	.name{
		font-size: 60px;
	}

	.poffeshn {
		margin-top: 13px;
		font-size: 40px;
	}
	.main1 {
		display: block;
		text-align: center;
		margin: 0 auto;
	}

	.main2 {
		display: block;
		text-align: center;
		margin: 0 auto;
	}

	.mainUP {
		display: block;
		padding-top: 20px;
		margin: 0 auto;
		text-align: center;
	}

	.mainDown {
		display: block;
		margin: 0 auto;
		padding-top: 20px;
		text-align: center;
	}

	.ball {
		display: none;
	}

	.container {
		padding-bottom: 0;
	}

	.block {
		margin: 0 auto;
		width: auto;
	}

	.block p {
		font-size: 15px;
	}

	.mainUP h2 {
		text-decoration: underline;
		margin: 0 auto;
	}

	.mainDown h2 {
		text-decoration: underline;
		margin: 0 auto;
	}
	.title {
		margin-bottom: 0;
	}
} 

